.formContainer{
    background-color: #022b2c;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.formWrapper {

background-repeat: no-repeat;
background-size: contain;
background-position: 7rem 1rem;
border: 5px solid#17c1c4;
width: 35rem;
background-color:transparent;
padding: 20px 60px;
border-radius: 10px;
display: flex;
flex-direction: column;
gap:10px;
align-items: center;
}
.form{
    background-color: rgba(8, 94, 95,0.5);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:15px;
}

.logo{
    color:white;
    font-weight: bold;
    font-size: 24px;
}
.title{
    color:white;
    font-size: 15px;
}
.input1,.input2,.input3 {
padding: 15px;
border:none;
outline: none;
background-color: transparent;
border-bottom:1px solid #a7bcff;
width: 15rem;
font-size: 300;
color:white;
}
input::placeholder{
    color:rgb(255, 255, 255);
    
}
.inputbtn{
    background-color: rgb(226, 91, 113);
    color:white;
    z-index: 2;
    width: 8rem;
    padding: 10px;
    font-weight: bold;
    border: none;
    cursor: pointer;
}
.inputbtn:hover{
    background-color: rgb(230, 67, 94);
}
.formWrapper p{
    color:white;
}
.form span{
    color:rgb(245, 78, 78);
}
@media only screen and (max-width:600px)
{
    .formWrapper{
width: fit-content;
padding: 0;
border: none;
background-position: 2rem 5rem;
    }
    .form{
        padding: 5px 5px 10px 5px;
    }
    .input1,.input2,.input3 {
        padding: 15px;
        border:none;
        outline: none;
        background-color: transparent;
        border-bottom:1px solid #a7bcff;
        width: 14rem;
        font-size: 300;
        color:white;
        }
        .labal{
            color:white;
            display: flex;
            align-items: center;
            width:25cqmax;
            cursor: pointer;
        }
    
}
